.sortable {
  border-spacing: 0;
}

.sortable td, .sortable th {
  padding: 10px;
}

.sortable td:first-child, .sortable th:first-child {
  border-top-left-radius: 4px;
}

.sortable td:last-child, .sortable th:last-child {
  border-top-right-radius: 4px;
}

.sortable th {
  cursor: pointer;
  text-align: left;
  text-transform: capitalize;
  vertical-align: baseline;
  white-space: nowrap;
  font-weight: normal;
}

.sortable th:hover {
  color: #efe27d;
}

.sortable th:hover:after {
  color: inherit;
  content: " ▸";
  font-size: 1.2em;
}

.sortable th:after {
  color: #0000;
  content: " ▸";
  font-size: 1.2em;
}

.sortable th.dir-d {
  color: #949393;
}

.sortable th.dir-d:after {
  color: inherit;
  content: " ▾";
}

.sortable th.dir-u {
  color: #e5e3e3;
}

.sortable th.dir-u:after {
  color: inherit;
  content: " ▴";
}

